.promo-text {
  height: 100%;
  width: 90%;
  -webkit-transform: translateX(-50%);
  -moz-transform: translateX(-50%);
  -ms-transform: translateX(-50%);
  -o-transform: translateX(-50%);
  transform: translateX(-50%);
  text-align: center;
  text-transform: uppercase;
  color: #fff;
  font-size: 28px;
  line-height: 24px;
  font-weight: 800;
  position: absolute;
  left: 50%;
  top: 85px;
  z-index: 2;
  text-shadow: 0 0 5px #232323;
  @media screen and (min-width: $medium-devices) {
    top: 150px;
    font-size: 40px;
    line-height: 35px;
  }
  @media screen and (min-width: $large-devices) {
    width: 72.7%;
    top: 250px;
    font-size: 48px;
    line-height: 54px;
  }
}
h2 {
