}
.advantages_text {
  width: 100%;
  @media screen and (min-width: $medium-devices) {
    width: 48%;
  }
  ul {
    margin: 0;
    padding: 0 0 0 25px;
    list-style: none;
  }
  ul > li {
    position: relative;
    line-height: 26px;
    &::before {
      content: "";
      position: absolute;
      left: -18px;
      top: 12px;
      display: inline-block;
      width: 6px;
      height: 6px;
      border-radius: 2px;
      -moz-border-radius: 2px;
      -webkit-border-radius: 2px;
      background: #92c944;
    }
  }
}
.advantages_text ol {
  margin: 0;
  padding: 0 0 0 25px;
  list-style: none;
  counter-reset: counter;
}
.advantages_text ol > li {
  counter-increment: counter;
  position: relative;
  line-height: 30px;
  color: #262626;
  font-size: 16px;
  line-height: 26px;
  &::before {
    content: counter(counter);
    position: absolute;
    left: -18px;
    top: 0;
    display: inline-block;
    color: #92c944;
    font-size: 18px;
    font-weight: 800;
  }
}
.advantages h3 {
  font-size: 20px;
  text-align: center;
  color: rgb(0, 128, 0);
  margin-top: 40px;
  font-weight: 800;
}
.advantages a {
  font-size: 24px;
  color: #fff;
  background: #92c944;
  text-align: center;
  cursor: pointer;
  display: inline-block;
  text-transform: uppercase;
  padding: 0 18px;
  outline: 0 none;
  text-decoration: none;
  font-weight: 600;
  border: 0;
  height: 50px;
  line-height: 50px;
  text-align: center;
  border-radius: 5px;
  -moz-border-radius: 5px;
  -webkit-border-radius: 5px;
  margin: 14px 0;
  &:hover {
    background: #67c927;
  }
}
